


















































.user {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 700px;
  margin: 0 auto;
  border: 1px solid #e7e7e7;
  background: #fbfbfb;
  padding: 20px;
  color: #333;
  transition: .3s;
  &:hover {
    background: #585f6c;
    color: rgba(255, 255, 255, .87);
  }
}

.user__left {
  line-height: 0;
  margin-right: 20px;
}

.user__name {
  font-weight: bold;
  font-size: 1rem;
  text-align: center;
  border-bottom: 1px solid #e7e7e7;
  margin: 0 20px 5px;
  padding-bottom: 5px;
}

.user__image {
  > img {
    max-width: 100px;
    max-height: 100px;
  }
}

.user__counters {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.user__counter {
  display: flex;
  flex-direction: column;
  margin: 5px 9px;
  text-align: center;
  &-name {
    font-weight: bold;
    font-size: .8rem;
  }
}

@media screen and (max-width: 700px) {
  .user {
    flex-direction: column;
  }

  .user__left {
    margin-bottom: 10px;
    margin-right: 0;
  }

  .user__name {
    margin-bottom: 10px;
    padding-bottom: 10px;
  }
}
